.footer {
  position: relative;
  padding: 50px 0;
  background-color: $white;
  max-width: 100%;
  overflow-x: hidden;
  width: 100%;
  border-top: 1px solid $purple;

  @media screen and (min-width: 768px) {
    padding: 100px 0;
  }

  &-container {
    position: relative;
    z-index: 2;
  }

  &__copyright {
    font-size: $font-sm;
    font-weight: 400;
    line-height: 130%;
  }

  &__img {
    position: absolute;
    z-index: 0;
    right: 0;
    top: 0;
    border: 0;
    object-fit: cover;
    object-position: center;
    max-height: 100%;
    height: 100%;
    width: 100%;
    max-width: 100%;

    &_desk {
      display: none;
      @media screen and (min-width: 768px) {
        display: block;
      }
    }

    &__mobile {
      @media screen and (min-width: 768px) {
        display: none;
      }
    }
  }

  &_list {
    margin-top: 38px;
    margin-bottom: 60px;

    @media screen and (min-width: 768px) {
      display: flex;
      align-items: center;
      justify-content: flex-start;;
    }
  }

  &_li {
    list-style-type: none;
    margin-bottom: 14px;
    padding-left: 0;
    &:first-child{
      margin-left: 0;
    }

    &:before{
      display: none;
    }

    a {
      text-decoration: none;
      font-size: $font-sm;
      font-style: normal;
      font-weight: 500;
      line-height: 24px;
      text-transform: capitalize;
      cursor: pointer;
    }

    @media screen and (min-width: 768px) {
      margin: 0 21px;
    }
  }

  &_link {
    text-decoration: none;
    font-size: $font-sm;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    text-transform: capitalize;
    cursor: pointer;
  }
}